import React from 'react'
import { CustomSpinner } from '../globalComponents'
import BlankTableError from '../commanComponent/BlankTableError'
import { setDateFormate } from '../../utils/common'


const GiveFeedback = (props) => {
    const { isLoading, toggleFeedbackKudosModal, givenFeedback } = props
    const renderGiveFeedback = () => {
        return (
            givenFeedback?.length > 0 ? givenFeedback?.map((item,index) =>
                <li key={index}>
                    <div
                        onClick={() => toggleFeedbackKudosModal("Feedback",item)}
                        className="subCategry"
                    >
                        <div className="row">
                            <div className="col-md-9 border-end">
                                <div className="d-flex gap-3">
                                    <figure className="mb-0">
                                        <img src="images/profile.png" alt="" width={40} />
                                    </figure>
                                    <div>
                                        <span>{item?.name}</span>
                                        <p>{item?.designation_name }</p>
                                    </div>
                                </div>
                                <strong className="pt-2">Feedback</strong>
                                <div dangerouslySetInnerHTML={{__html:item?.feedback_description}}>
                                {/* { } */}
                                </div>
                            </div>
                            <div className="col-md-3">
                                <div className="d-flex flex-wrap justify-content-center h-100 text-center ">
                                    <p className="w-100 mb-auto mt-2">{setDateFormate(item?.created_at,"DD MMM YYYY")}</p>
                                    <div className="mt-auto mb-4">
                                        <img src="images/arrow-right.svg" width={35} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            )
                :
                <div className='d-flex justify-content-center'>
                    <BlankTableError />
                </div>
        )
    }
    return (
        <div className="tab-pane fade" id="givenFeedback">
            <div className="cardBox myRequest mb-3">
                <div className="cardHead d-flex align-items-center">
                    <img
                        src="images/kpi-guidline.svg"
                        alt=""
                        width={40}
                        className="me-2 fw-bold"
                    />
                    <h3> Given Feedback</h3>
                </div>
                <div className="mykpiMain">
                    <ul className="subCategoryUl">
                        {isLoading ?
                            <div className='d-flex justify-content-center m-5'>
                                <CustomSpinner isShow={isLoading} size="md" />
                            </div>
                            :
                            renderGiveFeedback()
                        }

                    </ul>
                </div>
            </div>
        </div>


    )
}

export default GiveFeedback
