import React, { useEffect, useState } from 'react'
import FeedbackSummary from './FeedbackSummary'
import GiveFeedback from './GiveFeedback'
import GiveFeedbackBanner from './GiveFeedbackBanner'
import ReceiveFeedback from './ReceiveFeedback'
import Kudos from './Kudos'
import GiveFeedbackModal from './GiveFeedbackModal'
import TeamMembersModal from './TeamMembersModal'
import FeedbackKudosModal from './FeedbackKudosModal'
import { CustomTabs } from '../reuseableComponents'
import { useDispatch, useSelector } from 'react-redux'
import { employeeActions, feedbackActions } from '../../core/resource/actions'

const Feedback = () => {
    const dispatch = useDispatch()
    const {

        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending

    } = useSelector(state => state.employee)

    const {

        createFeedbackTeamRes,
        createFeedbackTeamError,
        createFeedbackTeamPending,

        getAllFeedbackTeamsPending,
        getAllFeedbackTeamsRes,
        getAllFeedbackTeamsError,

        createFeedbackPending,
        createFeedbackRes,
        createFeedbackError,

        givenFeedbackPending,
        givenFeedbackRes,
        givenFeedbackError,

        getReceivedFeedbackOnFilterPending,
        getReceivedFeedbackOnFilterRes,
        getReceivedFeedbackOnFilterError,

        getFeedbackDashboardDataPending,
        getFeedbackDashboardDataRes,
        getFeedbackDashboardDataError,


    } = useSelector(state => state.feedback)


    const [isShowGiveFeedback, setIsShowGiveFeedback] = useState(false)
    const [isShowTeamModal, setIsShowTeamModal] = useState(false)
    const [isShowFeedbackKudosModal, setIsShowFeedbackKudosModal] = useState(false)
    const [feedbackKudosModalName, setFeedbackKudosModalName] = useState('')
    const [allEmployeeList, setAllEmployeeList] = useState([])
    const [feedbackTeamData, setFeedbackTeamData] = useState([])
    const [feedbackDashboardData, setFeedbackDashboardData] = useState('')
    const [receiveFeedback, setReceiveFeedback] = useState('')
    const [givenFeedback, setGivenFeedback] = useState('')
    const [selectedFeedbackData,setSelectedFeedbackData]=useState('')

    const receivedFeedbackTabs = [
        { name: "All", onClickFun: (id) => getReceivedFeedBack(id), id: "all" },
        { name: "Team Leader", onClickFun: (id) => getReceivedFeedBack(id), id: "teamLeader" },
        { name: "Manager", onClickFun: (id) => getReceivedFeedBack(id), id: "manager" },
        { name: "Functional Head", onClickFun: (id) => getReceivedFeedBack(id), id: "functionalHead" },
    ];

    const getReceivedFeedBack = (type) => {
        const data = {
            // page: appliedCurrentPage ,
            // limit: pagination.limit,
            // limit: 1,
            type: type || '',
        }
    }

    const kudosTabs = [
        { name: "Received Kudos", onClickFun: (id) => getKudos(id), id: "receivedKudos" },
        { name: "Given Kudos", onClickFun: (id) => getKudos(id), id: "givenKudos" },
    ];
    const getKudos = (type) => {
        const data = {
            // page: appliedCurrentPage ,
            // limit: pagination.limit,
            // limit: 1,
            type: type || '',
        }
    }

    const toggleGiveFeedbackModal = () => {
        setIsShowGiveFeedback(!isShowGiveFeedback)
    }
    const toggleTeamMemberModal = () => {
        setIsShowTeamModal(!isShowTeamModal)
    }
    const toggleFeedbackKudosModal = (type,data) => {
        if(data){

            setSelectedFeedbackData(data)
        }else{
            setSelectedFeedbackData('')
        }
        setFeedbackKudosModalName(type)
        setIsShowFeedbackKudosModal(!isShowFeedbackKudosModal)
    }
    /* Feedback Tabs */
    const feedbackTabs = [
        { name: "Received Feedback",onClickFun: (id) => handleChangeFeedbackTab(id), id: "receivedFeedback" },
        { name: "Given Feedback",onClickFun: (id) => handleChangeFeedbackTab(id), id: "givenFeedback" },
        { name: "Kudos",onClickFun: (id) => handleChangeFeedbackTab(id), id: "kudos" },
    ];
    const handleChangeFeedbackTab =(id)=>{
        if(id==="receivedFeedback"){
            getReceivedFeedbackOnFilter()
        }
        else if(id ==="givenFeedback"){
            givenFeedbackData()
        }
        else if(id==="kudos"){

        }
    }

    useEffect(() => {

        getAllFeedbackTeam()
        getAllEmployee()
        getFeedbackDashboardData()
        getReceivedFeedbackOnFilter()
        
    }, [])


    /* Get All Employee List*/
    const getAllEmployee = () => {
        dispatch(employeeActions.getAllEmployeeRequest())
    }
    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes?.code === 200) {

                    setAllEmployeeList(getAllEmployeeRes?.data)
                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);

    /* Create Feedback Team */
    const createFeedbackTeam = (payload) => {
        dispatch(feedbackActions.createFeedbackTeamRequest(payload))
    }

    useEffect(() => {
        if (!createFeedbackTeamPending) {
            if (createFeedbackTeamError) {
                dispatch(feedbackActions.handleClearFeedbackStore("HANDLE_CLEAR_CREATE_FEEDBACK_TEAM_STORE"))
                return;
            }
            if (createFeedbackTeamRes) {
                if (createFeedbackTeamRes?.code === 200) {
                    dispatch(feedbackActions.handleClearFeedbackStore("HANDLE_CLEAR_CREATE_FEEDBACK_TEAM_STORE"))
                    toggleTeamMemberModal()
                    getAllFeedbackTeam()
                }
            }
        }
    }, [
        createFeedbackTeamRes,
        createFeedbackTeamError,
        createFeedbackTeamPending
    ]);

    /* Get All Feedback Team */
    const getAllFeedbackTeam = (payload) => {
        dispatch(feedbackActions.getAllFeedbackTeamsRequest(payload))
    }

    useEffect(() => {
        if (!getAllFeedbackTeamsPending) {
            if (getAllFeedbackTeamsError) {
                return;
            }
            if (getAllFeedbackTeamsRes) {
                if (getAllFeedbackTeamsRes?.code === 200) {
                    setFeedbackTeamData(getAllFeedbackTeamsRes?.data)
                }
            }
        }
    }, [
        getAllFeedbackTeamsRes,
        getAllFeedbackTeamsError,
        getAllFeedbackTeamsPending
    ]);

    /* Create Feedback */
    const createFeedback = (payload) => {
        dispatch(feedbackActions.createFeedbackRequest(payload))
    }

    useEffect(() => {
        if (!createFeedbackPending) {
            if (createFeedbackError) {
                dispatch(feedbackActions.handleClearFeedbackStore("HANDLE_CLEAR_CREATE_FEEDBACK_STORE"))
                return;
            }
            if (createFeedbackRes) {
                if (createFeedbackRes?.code === 200) {
                    dispatch(feedbackActions.handleClearFeedbackStore("HANDLE_CLEAR_CREATE_FEEDBACK_STORE"))
                    toggleGiveFeedbackModal()

                }
            }
        }
    }, [
        createFeedbackRes,
        createFeedbackError,
        createFeedbackPending
    ]);


    /*Get Feedback Dashboard Data */
    const getFeedbackDashboardData = () => {
        dispatch(feedbackActions.getFeedbackDashboardDataRequest())
    }

    useEffect(() => {
        if (!getFeedbackDashboardDataPending) {
            if (getFeedbackDashboardDataError) {
                return;
            }
            if (getFeedbackDashboardDataRes) {
                if (getFeedbackDashboardDataRes?.code === 200) {
                    setFeedbackDashboardData(getFeedbackDashboardDataRes?.data[0])

                }
            }
        }
    }, [
        getFeedbackDashboardDataRes,
        getFeedbackDashboardDataError,
        getFeedbackDashboardDataPending
    ]);

    /*Get Received Feedback On Filter */
    const givenFeedbackData = (payload) => {
        dispatch(feedbackActions.givenFeedbackRequest())
    }

    useEffect(() => {
        if (!givenFeedbackPending) {
            if (givenFeedbackError) {
                return;
            }
            if (givenFeedbackRes) {
                if (givenFeedbackRes?.code === 200) {
                    setGivenFeedback(givenFeedbackRes?.data)

                }
            }
        }
    }, [
        givenFeedbackRes,
        givenFeedbackError,
        givenFeedbackPending
    ]);


    /*Get Received Feedback On Filter */
    const getReceivedFeedbackOnFilter = (payload) => {
        dispatch(feedbackActions.getReceivedFeedbackOnFilterRequest(payload))
    }

    useEffect(() => {
        if (!getReceivedFeedbackOnFilterPending) {
            if (getReceivedFeedbackOnFilterError) {
                return;
            }
            if (getReceivedFeedbackOnFilterRes) {
                if (getReceivedFeedbackOnFilterRes?.code === 200) {
                    console.log(getReceivedFeedbackOnFilterRes?.data)
                    setReceiveFeedback(getReceivedFeedbackOnFilterRes?.data)

                }
            }
        }
    }, [
        getReceivedFeedbackOnFilterRes,
        getReceivedFeedbackOnFilterError,
        getReceivedFeedbackOnFilterPending
    ]);

    return (
        <>
            <FeedbackSummary feedbackDashboardData={feedbackDashboardData} />
            <GiveFeedbackBanner
                toggleGiveFeedbackModal={toggleGiveFeedbackModal}
            />
            <CustomTabs tabs={feedbackTabs} type={"feedback"} ulClass={"mb-3 feedBackTab"} />

            <div class="tab-content feedBackTabContent">
                <ReceiveFeedback
                    toggleFeedbackKudosModal={toggleFeedbackKudosModal}
                    receivedFeedbackTabs={receivedFeedbackTabs}
                    receivedFeedbackData={receiveFeedback}
                    isLoading={getReceivedFeedbackOnFilterPending}

                />
                <GiveFeedback
                    toggleFeedbackKudosModal={toggleFeedbackKudosModal}
                    givenFeedback={givenFeedback}
                    isLoading={givenFeedbackPending}
                />
                <Kudos
                    toggleFeedbackKudosModal={toggleFeedbackKudosModal}
                    kudosTabs={kudosTabs}
                />

            </div>
            {isShowGiveFeedback &&
                <GiveFeedbackModal
                    openModel={isShowGiveFeedback}
                    handleOpenClose={toggleGiveFeedbackModal}
                    toggleTeamMemberModal={toggleTeamMemberModal}
                    allEmployeeList={allEmployeeList}
                    feedbackTeamData={feedbackTeamData}
                    createFeedback={createFeedback}
                />}
            {isShowTeamModal &&
                <TeamMembersModal
                    openModel={isShowTeamModal}
                    handleOpenClose={toggleTeamMemberModal}
                    allEmployeeList={allEmployeeList}
                    createFeedbackTeam={createFeedbackTeam}
                />
            }
            {isShowFeedbackKudosModal &&
                <FeedbackKudosModal
                    openModel={isShowFeedbackKudosModal}
                    handleOpenClose={toggleFeedbackKudosModal}
                    feedbackKudosModalName={feedbackKudosModalName}
                    selectedFeedbackData={selectedFeedbackData}
                />
            }

        </>
    )
}

export default Feedback
