import React, { useEffect, useState } from 'react'
import { useCustomTranslation } from '../../translation'
import { CustomButton, CustomError } from '../globalComponents'
import { CustomRadio, CustomSwitch, Input, Label, RatingReview } from '../commanComponent'
import CustomSelect from '../commanComponent/CustomSelect'
import { CommonCKEditor } from '../reuseableComponents'
import { giveFeedbackOption } from '../../utils/Constants.config'
import { validateSchema } from '../../utils/ValidationHelper'
import { createFeedbackSchema } from '../../utils/ValidationSchema'
import { multiSelectIDs } from '../../utils/common'
const GiveFeedbackModal = (props) => {
    const { t } = useCustomTranslation()
    const { openModel, handleOpenClose, toggleTeamMemberModal, allEmployeeList, feedbackTeamData, createFeedback } = props
    const [errors, setErrors] = useState({})
    const [rating, setRating] = useState(0)
    const [editorData, setEditorData] = useState('')
    const [giveFeedbackFormData, setGiveFeedbackFormData] = useState({
        type: false,
        feedback_to: [],
        feedback_title: "",
        feedback_description: "",
        feedback_type: "",
        selectFeedbackTeam: null,
        giveRating: false
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setGiveFeedbackFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };


    const onSubmitFormData = async () => {
        const employeeIds=  multiSelectIDs(giveFeedbackFormData?.feedback_to)
        const params = {
            type: giveFeedbackFormData?.type?"team":"individual",
            feedback_to: employeeIds || [],
            feedback_title:giveFeedbackFormData?.feedback_title,
            feedback_description:editorData,
            feedback_rating: rating,
            feedback_type: giveFeedbackFormData?.feedback_type,
            feedback_team: giveFeedbackFormData?.selectFeedbackTeam?.value||null,
        }
        createFeedback(params)
        // const validationResponse = await validateSchema(createFeedbackSchema, giveFeedbackFormData);
        // if (validationResponse.isValid) {
           
        // }
        // else {
        //     setErrors(validationResponse.errors);
        // }
    }

    useEffect(() => {
        return () => {
            resetFormData()
        }
    }, [openModel])
    const resetFormData = () => {
        setGiveFeedbackFormData({
            type: false,
            feedback_to: [],
            feedback_title: "",
            feedback_description: "",
            feedback_rating: '',
            feedback_type: "",
            selectFeedbackTeam: '',
            giveRating: false
        })
    }
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "700px"
                }}
                id="offcanvasExampleLabel"
            >

                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Give Feedback</h5>
                    <button type="button"
                        className="btn-close"
                        data-bs-dismiss="offcanvas"
                        aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />

                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="form-group mb-3 d-flex align-items-center gap-4">
                            <Label text={"Select Feedback Type"} />
                            <CustomSwitch
                                id="switchques1"
                                name="type"
                                checked={giveFeedbackFormData?.type}
                                onChange={(e) => handleFromChange(e)}
                                labelLeft="Individual"
                                labelRight="Team"
                                className="text-light "
                            />
                            {/* <span className="toggleSwitch d-flex align-items-center gap-2 text-light">
                                Individual
                                <Input
                                    id="switchques1"
                                    className="d-none"
                                    type="checkbox"
                                    // defaultValue={0}
                                    checked={giveFeedbackFormData?.type}
                                    name="type"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                <label htmlFor="switchques1" className="mb-0">
                                    Toggle
                                </label>
                                Team
                            </span> */}
                        </div>
                        {!giveFeedbackFormData?.type ? <div className="form-group mb-3">
                            <Label text={"Select Recipient"} />

                            <CustomSelect
                                data={allEmployeeList || []}
                                placeholder={t("placeholders.employee")}
                                value={giveFeedbackFormData?.feedback_to}
                                handleChange={(selectedOption) => handleFromChange(selectedOption, "feedback_to")}
                                isMulti={true}
                            />
                        </div>
                            : <div className="form-group mb-3 d-flex align-items-center align-items-center gap-3">

                                <div className="w-75">
                                    <Label text={"Select Team"} />
                                    <CustomSelect
                                        data={feedbackTeamData || []}
                                        placeholder={t("placeholders.team")}
                                        value={giveFeedbackFormData?.selectFeedbackTeam}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "selectFeedbackTeam")}
                                        name="selectFeedbackTeam"
                                        
                                    />
                                </div>
                                <CustomButton
                                    classNames="btn btn-primary gradientBg mt-auto"
                                    text={t("btns.createTeam")}
                                    handleClick={() => toggleTeamMemberModal()}
                                />

                            </div>}
                        <div className="form-group mb-3">
                            <Label text={"Title"} required={true}/>
                            <Input
                                type="text"
                                name="feedback_title"
                                placeholder="Enter here.."
                                value={giveFeedbackFormData?.feedback_title}
                                onChange={(e) => handleFromChange(e)}
                            />
                            {errors?.title && (
                                <CustomError text={errors.title} />
                            )}
                        </div>

                        <div className="form-group mb-3">
                            <Label text={"Enter Feedback"} />
                            <div>
                                    <CommonCKEditor
                                        editorData={editorData}
                                        setEditorData={setEditorData}
                                        bodyData={''}
                                        editorSize="sm"
                                    />
                            </div>
                        </div>

                        <div className="form-group mb-2">
                            <CustomSwitch
                                id="switchques3"
                                name="giveRating"
                                checked={giveFeedbackFormData?.giveRating}
                                onChange={(e) => handleFromChange(e)}
                                labelRight="Give Rating( if Required)"
                                className="text-primary fw-semibold"
                            />
                        </div>
                        {giveFeedbackFormData?.giveRating && <div className="form-group mb-3 d-flex gap-3 align-items-center">
                            <div className="d-flex gap-2 ">
                                <RatingReview rating={rating} setRating={setRating} />
                            </div>
                            {
                            /* <span>OR</span>
                            <ul className="emojilable">
                                <li>
                                    <input id="npsQ1" className="d-none" type="radio" name="npsQ" />
                                    <label htmlFor="npsQ1">1</label>
                                </li>
                                <li>
                                    <input id="npsQ2" className="d-none" type="radio" name="npsQ" />
                                    <label htmlFor="npsQ2">2</label>
                                </li>
                                <li>
                                    <input id="npsQ3" className="d-none" type="radio" name="npsQ" />
                                    <label htmlFor="npsQ3">3</label>
                                </li>
                                <li>
                                    <input id="npsQ4" className="d-none" type="radio" name="npsQ" />
                                    <label htmlFor="npsQ4">4</label>
                                </li>
                                <li>
                                    <input id="npsQ5" className="d-none" type="radio" name="npsQ" />
                                    <label htmlFor="npsQ5">5</label>
                                </li>
                            </ul> */}
                        </div>}
                        <div className="form-group mb-3 d-flex align-items-center gap-3">
                            <CustomRadio
                                data={giveFeedbackOption}
                                name="feedback_type"
                                handleChange={(e)=>handleFromChange(e)}
                                selectedValue={giveFeedbackFormData?.feedback_type}
                            />

                        </div>

                        {/* <div className="form-group mb-3">
                            <ul>
                                <li>
                                    <div className="form-check">
                                        <input
                                            type="checkbox"
                                            className="form-check-input"
                                            id="kpicheck1"
                                        />
                                        <label
                                            htmlFor="kpicheck1"
                                            className="form-check-label fw-medium text-black"
                                        >
                                            KPI 1 (KPI Name)
                                        </label>
                                    </div>
                                </li>
                                <li>
                                    <div className="form-check">
                                        <input
                                            type="checkbox"
                                            className="form-check-input"
                                            id="kpicheck2"
                                        />
                                        <label
                                            htmlFor="kpicheck2"
                                            className="form-check-label fw-medium text-black"
                                        >
                                            KPI 2 (KPI Name)
                                        </label>
                                    </div>
                                </li>
                                <li>
                                    <div className="form-check">
                                        <input
                                            type="checkbox"
                                            className="form-check-input"
                                            id="kpicheck3"
                                        />
                                        <label
                                            htmlFor="kpicheck3"
                                            className="form-check-label fw-medium text-black"
                                        >
                                            KPI 3 (KPI Name)
                                        </label>
                                    </div>
                                </li>
                                <li>
                                    <div className="form-check">
                                        <input
                                            type="checkbox"
                                            className="form-check-input"
                                            id="kpicheck4"
                                        />
                                        <label
                                            htmlFor="kpicheck4"
                                            className="form-check-label fw-medium text-black"
                                        >
                                            KPI 4 (KPI Name)
                                        </label>
                                    </div>
                                </li>
                            </ul>
                        </div> */}
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">

                        <CustomButton
                            classNames="btn btn-primary gradientBg "
                            text={t("btns.submit")}
                            handleClick={() => onSubmitFormData()}

                        />


                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>

    )
}

export default GiveFeedbackModal
