import React from 'react'
import { setDateFormate } from '../../utils/common'
import { RatingReview } from '../commanComponent'

const FeedbackKudosModal = (props) => {
    const { openModel, handleOpenClose, feedbackKudosModalName,selectedFeedbackData } = props
    return (
        <>
        <div className={`modal fade feedbackModal ${openModel ? 'show' : ''}`} id="feedbackModal"
            tabIndex="-1"
            style={{ display: openModel ? 'block' : 'none', }}
            aria-hidden={!openModel}
        >
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content rounded-1 border-0">
                    <div className="modal-header border-0">
                        <h1 className="modal-title fs-5 fw-bold text-primary">{feedbackKudosModalName}</h1>
                        <button
                            type="button"
                            className="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                            onClick={() => handleOpenClose()}
                        />
                    </div>
                    <div className="modal-body ">
                    {feedbackKudosModalName === "Kudos"&&<div class="d-flex gap-3 justify-content-between align-items-center mb-3">
                            <strong class="pt-0">Appreciation By</strong>
                            <p>{setDateFormate(selectedFeedbackData?.created_at,"DD MMM YYYY")}</p>
                        </div>}
                        <div className="subCategry">
                            <div className="d-flex gap-3 justify-content-between">
                                <div className="d-flex gap-3">
                                    <figure className="mb-0">
                                        <img src="images/profile.png" alt="" width={40} />
                                    </figure>
                                    <div>
                                        <span>{selectedFeedbackData?.name}</span>
                                        <p>{selectedFeedbackData?.designation_name}</p>
                                    </div>
                                </div>
                                {feedbackKudosModalName === "Feedback" &&<p>{setDateFormate(selectedFeedbackData?.created_at,"DD MMM YYYY")}</p>}
                            </div>
                            <strong className="pt-2">Feedback</strong>
                            <p>
                               {selectedFeedbackData?.feedback_description}
                            </p>
                            {feedbackKudosModalName === "Feedback" &&
                                <>
                                    <strong className="pt-2">Rating</strong>
                                    <div className="d-flex gap-3 mb-3">
                                        <b>
                                            {selectedFeedbackData?.feedback_rating}<span>/ 5</span>
                                        </b>
                                       <RatingReview rating={Number(selectedFeedbackData?.feedback_rating)} />
                                    </div>
                                </>
                            }
                            {feedbackKudosModalName === "Kudos" &&<div class="w-12  ">
                                <strong>Points</strong>
                                <b class="purpleTxtColor">50</b>
                            </div>}
                        </div>
                    </div>
                </div>
            </div>

        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default FeedbackKudosModal
