import React from 'react'

const FeedbackSummary = (props) => {
    const {feedbackDashboardData}=props
    return (
        <div className="">
            <div className="cardHead d-flex justify-content-between align-items-center">
                <h3 className="text-primary"> Summary</h3>
            </div>
            <div className=" leaveAppli position-relative z-1">
                <div className="row selectModal px-md-1 ">
                    <div className="col-md-3 px-md-2 mb-3">
                        <div className="totalBx h-100 bg-white shadow-none">
                            <div className="d-flex justify-content-between align-items-start text-end">
                                <figure>
                                    <img src="images/average-rating-Icon.svg" alt="" width={40} />
                                </figure>
                                <h3 className="fw-bolder my-0">
                                    Feedback Received <span className="mt-4">{feedbackDashboardData?.received_feedback }</span>
                                </h3>
                            </div>
                            <img
                                src="images/annualrating-line.svg"
                                className="cornerLine"
                                alt=""
                                width={65}
                            />
                        </div>
                    </div>
                    <div className="col-md-3 px-md-2 mb-3">
                        <div className="totalBx h-100 bg-white shadow-none">
                            <div className="d-flex justify-content-between align-items-start text-end">
                                <figure>
                                    <img src="images/average-rating-Icon.svg" alt="" width={40} />
                                </figure>
                                <h3 className="fw-bolder my-0">
                                    Feedback Given <span className="mt-4">{feedbackDashboardData?.given_feedback }</span>
                                </h3>
                            </div>
                            <img
                                src="images/cornerline-review.svg"
                                className="cornerLine"
                                alt=""
                                width={45}
                            />
                        </div>
                    </div>
                    <div className="col-md-3 px-md-2 mb-3">
                        <div className="totalBx h-100 bg-white shadow-none">
                            <div className="d-flex justify-content-between align-items-start text-end">
                                <figure>
                                    <img src="images/average-rating-Icon.svg " alt="" width={40} />
                                </figure>
                                <h3 className="fw-bolder my-0">
                                    Received Kudos
                                    <span className="mt-4">{feedbackDashboardData?.received_feedback }</span>
                                </h3>
                            </div>
                            <img
                                src="images/cornerline-review.svg"
                                className="cornerLine"
                                alt=""
                                width={45}
                            />
                        </div>
                    </div>
                    <div className="col-md-3 px-md-2 mb-3">
                        <div className="totalBx h-100 bg-white shadow-none">
                            <div className="d-flex justify-content-between align-items-start text-end">
                                <figure>
                                    <img src="images/average-rating-Icon.svg" alt="" width={40} />
                                </figure>
                                <h3 className="fw-bolder my-0">
                                    Given Kudos <span className="mt-4">{feedbackDashboardData?.given_kudos }</span>
                                </h3>
                            </div>
                            <img
                                src="images/firstlineBlue.svg"
                                className="cornerLine"
                                alt=""
                                width={75}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default FeedbackSummary
