import React from 'react'
import feedBack1 from "../../images/feedback1.json"
import Lottie from 'react-lottie-player'
import { CustomButton } from '../globalComponents'
import { useCustomTranslation } from '../../translation'
const GiveFeedbackBanner = (props) => {
    const {t}=useCustomTranslation()
    const { toggleGiveFeedbackModal } = props
    return (
        <div className="cardBox mb-3 createNewMrf">
            <div className="row align-items-center">
                <div className="col-md-9 col-xl-10">
                    <div className="row align-items-center">
                        <div className="col-md-3">
                            <Lottie
                                loop
                                animationData={feedBack1}
                                background="transparent"
                                speed="1"
                                play
                                style={{ width: "100%", height: "124px" }}
                                autoPlay></Lottie>

                        </div>
                        <div className="col-md-9">
                            <div className="cardHead pb-0">
                                <h3>
                                    Give <span>Feedback</span>
                                </h3>
                                <p className="mb-0">
                                    Feedback is an important part of a corporate culture and can help employees improve their performance and grow professionally.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-3 col-xl-2">
                    <CustomButton
                        classNames="btn btn-primary gradientBg w-100"
                        text={t("btns.giveFeedback")}
                        handleClick={() => toggleGiveFeedbackModal()}
                    />
                    {/* <a
                        href="#appraiseOffcanvas"
                        data-bs-toggle="offcanvas"
                        className="btn btn-primary gradientBg w-100"
                    >
                        Give Feedback
                    </a> */}
                </div>
            </div>
        </div>
    )
}

export default GiveFeedbackBanner
