import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../globalComponents'
import { useCustomTranslation } from '../../translation'
import { CustomMultiSelect, Input, Label } from '../commanComponent'
import CustomSelect from '../commanComponent/CustomSelect'
import { validateSchema } from '../../utils/ValidationHelper'
import { createTeamSchema } from '../../utils/ValidationSchema'
import { multiSelectIDs } from '../../utils/common'
const TeamMembersModal = (props) => {
    const { t } = useCustomTranslation()
    const { openModel, handleOpenClose, allEmployeeList, createFeedbackTeam } = props
    const [errors, setErrors] = useState({})
    const [createTeam, setCreateTeam] = useState({
        teamName: '',
        employees: null
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreateTeam(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const onSubmitFormData = async () => {
        const validationResponse = await validateSchema(createTeamSchema, createTeam);
        if (validationResponse.isValid) {
            const params = {
                "name": createTeam?.teamName,
                "emp_ids": multiSelectIDs(createTeam?.employees)
            }

            createFeedbackTeam(params)
        }
        else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return () => {
            resetFormData()
        }
    }, [openModel])
    const resetFormData = () => {
        setCreateTeam({
            teamName: '',
            employees: null
        })
    }

    return (
        <>
            <div className={`modal fade selectTeamMemeber ${openModel ? 'show' : ''}`} id="selectTeam"
                tabIndex="-1"
                style={{ display: openModel ? 'block' : 'none', }}
                aria-hidden={!openModel}
            >
                <div className="modal-dialog modal-dialog-centered" style={{ maxWidth: "530px" }}>
                    <div className="modal-content rounded-0 border-0">
                        <div className="modal-header">
                            <h1 className="modal-title fs-6 text-primary fw-semibold">
                                Create Team
                            </h1>
                            <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                                onClick={() => handleOpenClose()}
                            />
                        </div>
                        <div className="modal-body">
                            <div className="formPanel row">
                                <div className="col-12">
                                    <div className="form-group mb-3">
                                        <Label text={t("labels.teamName")} required={true} />
                                        <Input
                                            type="text"
                                            placeholder={t("placeholders.teamName")}
                                            value={createTeam?.teamName}
                                            name="teamName"
                                            onChange={(e) => handleFromChange(e)}
                                        />
                                        {errors?.teamName && (
                                            <CustomError text={errors.teamName} />
                                        )}
                                    </div>
                                </div>
                                <div className="col-12">
                                    <div className="form-group mb-3">
                                        <Label text={t("labels.selectEmployee")} required={true} />
                                        <CustomMultiSelect
                                            optionsData={allEmployeeList}
                                            placeholder={t("placeholders.employees")}
                                            values={createTeam?.employees}
                                            onChange={(selectedOption) => handleFromChange(selectedOption, "employees")}
                                        />
                                        {/* <CustomSelect
                                            data={allEmployeeList || []}
                                            placeholder={t("placeholders.employees")}
                                            value={createTeam?.employees}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "employees")}
                                            isMulti={true}
                                        /> */}
                                        {errors?.employees && (
                                            <CustomError text={errors.employees} />
                                        )}
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div className="modal-footer justify-content-end ">
                            <CustomButton
                                classNames="btn btn-oreang btnShadow text-white "
                                text={t("btns.save")}
                                handleClick={() => onSubmitFormData()}
                            />
                        </div>
                    </div>

                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default TeamMembersModal
